.photo {
  width: 120px;
}
.castList {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 20px;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}
.castItem {
  list-style: none;
  width: 200px;
}
